import Swal from "sweetalert2";

export function viewLive(props1) {
  Swal.fire({
    imageUrl: `${props1}`,
    imageHeight: 400,
    imageWidth: 800,
    width: 800,
    // title: `<div class="title">${props2}</div>`,
    background: "#fafafa",
    // toast: true,
    reverseButtons: true,
    // icon: 'info',
    iconColor: "#002e63",
    // html:
    // `<div class="text-justify">${props3}</div>`,
    showCloseButton: true,
    showCancelButton: false,
    focusConfirm: false,
    showConfirmButton: false,
    confirmButtonText: `<div class="okButton"><i class="fa fa-thumbs-up"></i><a> Great</a></div>`,
    confirmButtonAriaLabel: "Thumbs up, great!",
    cancelButtonText: `<div class="searchButton"><a href={'/'}>Effectuer une recherche</a></div>`,
    // '<i class="fa fa-thumbs-down"></i>',
    cancelButtonAriaLabel: "Thumbs down",
    confirmButtonColor: "#096aa1",
    cancelButtonColor: "#064b86",
    footer: "Note: Exemple Vivant",
  });
}
